.header {
  background-color: #000;
  padding: 20px;
  text-align: left;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  display: flex;
  align-items: center;
}

.heart-icon {
  color: #ff0000;
  margin-right: 10px;
}

h1 {
  color: #ff0000;
  margin-bottom: 10px;
}

.nav {
  display: none;
}

.nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.nav li {
  display: inline;
  margin-right: 20px;
}

.nav a {
  text-decoration: none;
  color: #ff0000;
  border-radius: 5px;
  padding: 10px;
  transition: background-color 0.3s ease;
}

.nav a:hover {
  background-color: #ff0000;
  color: #000;
}

.menu-toggle {
  cursor: pointer;
  display: none;
}

@media screen and (max-width: 768px) {
  .nav {
    display: block;
  }

  .nav ul {
    display: flex;
    flex-direction: column;
  }

  .nav li {
    margin-right: 0;
    margin-bottom: 10px;
  }

  .menu-toggle {
    display: block;
  }

  .show-menu {
    display: block;
  }
}
